.menu {
  -ms-flex: none;
      flex: none;
  border-bottom: 1px solid #111;
  padding: .5em;
}

.category {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: baseline;
      align-items: baseline;
  margin-bottom: .25em;
}

.category__label {
  -ms-flex: none;
      flex: none;
  width: 7em;
  padding: 0 .25em;
}

.menu__input {
  background-color: #999;
  border: 1px solid #666;
  padding: 2px .25em;
}

.menu__clear {
  padding: .1em .5em;
  -ms-flex-item-align: center;
      align-self: center;
  margin-left: .5em;
  cursor: pointer;
  border-radius: .15em;
  -webkit-transition: background-color 150ms;
  -o-transition: background-color 150ms;
  transition: background-color 150ms;
}

.menu__clear:hover {
  background-color: #111;
}

.menu__text {
  margin-left: auto;
}



