.macro {
  -webkit-transition: background-color 200ms;
  -o-transition: background-color 200ms;
  transition: background-color 200ms;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  padding: .5em 0;
  border-bottom: solid 1px #333;
}

.macro:hover {
  background-color: #1a1a1a;
}

.macro > div {
  padding: 0 .5em;
}

.macro > div:not(:first-child) {
  border-left: solid 1px #333;
}

.macro__description {
  width: 20em;
  -ms-flex: none;
      flex: none;
}

.macro__header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: center;
      align-items: center;
  margin-bottom: .5em;
}

.macro__label {
  margin: .25em 0;
}

.macro__label:not(:last-child)::after {
  content: ',';
  margin-right: .5em;
}

.macro__icon {
  width: 2em;
  height: 2em;
  border: solid 1px #666;
  border-radius: .15em;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin-right: .5em;
  display: block;
  -ms-flex: none;
      flex: none;
}

.macro__tags {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.macro__tag {
  color: #aaa;
  background: #333;
  padding: .15em .5em;
  border-radius: .25em;
  white-space: nowrap;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: .25em .25em 0 0;
  font-size: .9em;
}


@media (max-width: 1000px) {
  .macro__description {
    width: 15em;
  }
}

@media (max-width: 600px) {
  .macro {
    display: block;
  }

  .macro__description {
    width: 100%;
  }

  .macro__lines {
    margin-top: 1em;
  }
}



