.menu__tags {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.menu__tag {
  color: #aaa;
  background: #333;
  padding: .25em .5em;
  border-radius: .25em;
  cursor: pointer;
  white-space: nowrap;
  margin-bottom: .25em;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.menu__tag--selected {
  background-color: #000;
}

.menu__tag:not(:last-child) {
  margin-right: .5em;
}



